import React,{ useState } from 'react';
import images from './images/logo.png';
import {Link} from 'react-scroll';

function Navbar() {
    const [nav,setnav] = useState(false);

    const changeBackground = () => {
        if (window.scrollY >= 50) {
            setnav(true);
        } else {
            setnav(false)
    }
}

    window.addEventListener('scroll',changeBackground);

    return (
        <nav className={nav ? "nav active" :"nav"}>
            <Link to='main' className='images' smooth={true} duration={2000}>
                <img src={images} alt=''/>
            </Link>
            <input className='menu-btn' type='checkbox' id='menu-btn' />
            <label className='menu-icon' for='menu-btn' for='menu-btn'>
                <span className='nav-icon'></span>
            </label>
            <ul className='menu'>
                <li><Link to='main'smooth={true} duration={1000}>Main</Link></li>
                <li><Link to='services'smooth={true} duration={1000}>Services</Link></li>
                <li><Link to='mentors'smooth={true} duration={1000}>Mentors</Link></li>
                <li><Link to='about'smooth={true} duration={1000}>About</Link></li>
                <li><Link to='contact'smooth={true} duration={1000}>Contact</Link></li>
            </ul>
        </nav>
    )
}

export default Navbar;